@import '../mixins';

.usersGet {
	padding: 140px 0 70px;

	@include lg {
		padding: 140px 60px 70px;
	}
	@include md {
		padding: 140px 32px 70px;
	}
	@include sm {
		padding: 140px 16px 70px;
	}

	.title {
		text-align: center;
		margin-bottom: 50px;
	}

	.primary-btn {
		margin: 50px auto 0;
	}


	.error {
		text-align: center;
		color: #cb3d40;
	}
}
.users {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	gap: 29px;

	@include md {
		gap: 16px;
	}
	@include sm {
		justify-content: center;
		gap: 20px;
	}
}